.noScroll {
  overflow: hidden;
}

::selection {
  background: #ff8484;
}

.scrollableSection {
  overflow-y: auto;
  overflow-x: hidden;
}

.scroll4::-webkit-scrollbar {
  width: 7px;
}

.scroll4::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 15px;
}

.scroll4::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 15px;
}

@media (max-width: 600px) {
  .noScroll {
    overflow: auto;
  }
  .scrollableSection {
    overflow-y: hidden;
    overflow-x: hidden;
  }
}
